import { SxProps } from "@rp/react/web/mui/core"
import { Share } from "@rp/react/web/mui/icons"

type Props = {
  size?: number
  sx?: SxProps
  shared?: boolean
}

export const ShareIcon = ({ sx, size = 20 }: Props) => {
  return <Share sx={{ ...sx, width: size, height: size, fontSize: size }} />
}
